import uniCard from "@/components/uni-card/uni-card.vue";
import icard from "../../static/dist/card/index.js";
import {getAllRole} from "../../static/utils/api.js";
const login = require('../../static/utils/utils').Login;
const query = require('../../static/utils/utils').Query;
const Query = new query();
const Login = new login();

var _this;
export default {
	components: {uniCard,icard},
	data() {
		return {
			userInfo:{},
		    roleId:"",
			isRoot:"" ,         //特别设置的超级权限用户.
			roleName:"",        //权限对应的名称
			allRole:[]         //存放获取到的所有的专业
		}
	},

	//进行登录的更新
	onShow(){
	  _this = this
	  wx.getStorage({
	  	key:"userInfo",
		success:(res)=>{
		   let id = {
			   id:res.data.id
		   }
		   Query.findUser(id)
		   .then(data=>{
			   //console.log("返回的用户的信息",data.data.records[0]) 
			   _this.userInfo = data.data.records[0];
			   let isRoot =  data.data.records[0].isRoot;
			   if(isRoot){
				   _this.roleId = 0;
				   _this.roleName = "超级用户"
			   }else{
				   uni.getStorage({
				   	key:"nowInProject",
					success:(res)=>{
						
		 				//设置普通权限并且,获取对应权限用户权限的名称
						
						_this.roleId = res.data.roleId;
						_this.getRoleName();
					 }  
				   })
			   }
		   })
		},
		fail:()=>{
		  uni.redirectTo({
		  	url:'../login/login'
		  })
		}
	  })
	},
	
	methods: {
		//获取对应权限的名称
		getRoleName:function(){
			_this = this;
			uni.request({
				url:getAllRole,
				method:'GET',
				dataType:'json'
			})
			.then(data=>{
				//根据查询到的权限的名臣对roleName进行赋值
				let allRole = data[1].data.data;
				_this.allRole = allRole;
				allRole.forEach((item,index)=>{
					if(item.id === _this.roleId){ 
						_this.roleName = item.name;
					}
				});
			})
			.catch(error=>{
				uni.showToast({
					icon:"none",
					duration:1000,
					title:"获取失败"
				})
			})
		},
		
		//退出登录
		logout:function(){
		   uni.removeStorage({
		   	 key:'userInfo',
			 success:()=>{
				uni.redirectTo({
					url:'../login/login'
				})
			 }
		   })
		}
	}
}
